.MainContainer {

  box-sizing: border-box;
  padding: 6% 8%;
}

.MainContentWrapper {
  display: flex;
}

.BoldText{
  color: black;
}

.TableLink {
  text-decoration: none;
  width: 100%;
}


.FilterItemsWrapper {
  display: flex;
  flex-direction: column;
  width: 180px;
}

.Container {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 16px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.MainHeading {
  font-weight: 600;
}
.Container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.Checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #eee;
}

.Container:hover input ~ .Checkmark {
  background-color: #ccc;
}

.Container input:checked ~ .Checkmark {
  background-color: #2196f3;
}

.Checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.Container input:checked ~ .Checkmark:after {
  display: block;
}

.Container .Checkmark:after {
  left: 9px;
  top: 5px;
  width: 4px;
  height: 9px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

